import { useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import gsap from 'gsap';
import ScrollToPlugin from 'gsap/ScrollToPlugin';

function PortfolioList(){
	let navigate=useNavigate();

	useEffect(() => {
		gsap.registerPlugin(ScrollToPlugin);

		function portfolioListInit(){
			gsap.to(window, {scrollTo: 0, duration: 0});
		}

		portfolioListInit();

		let gnb=document.getElementById("gnb");
		let gnbList=gnb.firstElementChild.children;

		let n=0;
		let total=gnbList.length;

		for(let i=0; i<total; i++){
			gnbList[i].addEventListener("click", e => {
				e.preventDefault();

				n=i;

				// console.log(n);

				navigate("/", {state: "page"+(n+1)});
			});
		}
	});

	return(
		<div className="container">
			<header>
				<nav id="gnb">
					<ul>
						<li><a href="#page1">page1</a></li>
						<li><a href="#page2">page2</a></li>
						<li><a href="#page3">page3</a></li>
						<li><a href="#page4">page4</a></li>
						<li><a href="#page5">page5</a></li>
						<li><a href="#page6">page6</a></li>
					</ul>
				</nav>
			</header>
			<div className="portfolio">
				<ul>
					<li>
						<strong>Portfolio1</strong>
						<button onClick={() => navigate("/portfolio/portfolio1",  {state: "portfolio1"})}>page link</button>
					</li>
					<li>
						<strong>Portfolio2</strong>
						<button onClick={() => navigate("/portfolio/portfolio2",  {state: "portfolio2"})}>page link</button>
					</li>
					<li>
						<strong>Portfolio3</strong>
						<button onClick={() => navigate("/portfolio/portfolio3",  {state: "portfolio3"})}>page link</button>
					</li>
					<li>
						<strong>Portfolio4</strong>
						<button onClick={() => navigate("/portfolio/portfolio4",  {state: "portfolio4"})}>page link</button>
					</li>
					<li>
						<strong>Portfolio5</strong>
						<button onClick={() => navigate("/portfolio/portfolio5",  {state: "portfolio5"})}>page link</button>
					</li>
					<li>
						<strong>Portfolio6</strong>
						<button onClick={() => navigate("/portfolio/portfolio6",  {state: "portfolio6"})}>page link</button>
					</li>
				</ul>
			</div>
		</div>
	);
}

export default PortfolioList;