import { useEffect } from 'react';
import { useLocation } from "react-router-dom";
import gsap from 'gsap';
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import ScrollTrigger from '@terwanerik/scrolltrigger';
import Navigation from "./Navigation";

function Header(){
	let location=useLocation();
	let link=location.state;

	// console.log(link);

	useEffect(() => {
		gsap.registerPlugin(ScrollToPlugin);

		let n=0;
		let prevN;
		let total;
		let t;

		let pages=document.getElementsByTagName("section");
		let gnb=document.getElementById("gnb");
		let gnbList=gnb.firstElementChild.children;
		total=pages.length;

		function init(){
			if(link){
				let targety=document.getElementById(link).offsetTop;

				gsap.to(window, {scrollTo: targety, duration: 0.5});
			}
		}

		init();

		function scrollListener(){
			t=window.scrollY;

			if(t < pages[1].offsetTop){
				n=0;
			}
			else if(t < pages[2].offsetTop){
				n=1;
			}
			else if(t < pages[3].offsetTop){
				n=2;
			}
			else if(t < pages[4].offsetTop){
				n=3;
			}
			else if(t < pages[5].offsetTop){
				n=4;

				if(window.innerHeight+t === document.body.scrollHeight){
					n=5;
				}
			}
			else{
				n=5;
			}

			if(t > 150){
				if(gnb.classList.contains("fixed") === false){
					gnb.classList.add("fixed");
				}
			}
			else{
				if(gnb.classList.contains("fixed") === true){
					gnb.classList.remove("fixed");
				}
			}

			if(n === prevN) return;
			prevN=n;

			for(let i=0; i<total; i++){
				if(i === n){
					if(gnbList[i].classList.contains("active") === false){
						gnbList[i].classList.add("active");
					}
				}
				else{
					if(gnbList[i].classList.contains("active") === true){
						gnbList[i].classList.remove("active");
					}
				}
			}
		}

		window.addEventListener("scroll", scrollListener);

		for(let i=0; i<total; i++){
			gnbList[i].addEventListener("click", e => {
				e.preventDefault();

				n=i;

				let targety=pages[n].offsetTop;

				gsap.to(window, {scrollTo: targety, duration: 0.5});
			});
		}

		const trigger=new ScrollTrigger({
			trigger: {
				once: true,
				toggle: {
					class: {
						in: "active",
						out: "inactive"
					}
				},
				offset: {
					viewport: {
						x: 0,
						y: 0.25
					}
				}
			}
		});

		trigger.add("section[id^=page]");

		return(() => {
			window.removeEventListener("scroll", scrollListener);
		});
	});

	return(
		<header>
			<Navigation />
		</header>
	);
}

export default Header;