import { useNavigate } from "react-router-dom";

function Page(props){
	let navigate=useNavigate();

	let {id, title, sub}=props.propsValue;

	return(
		<section id={id}>
			<h2>{title}</h2>
			<p>{sub}</p>
			<button onClick={() => navigate("/portfolio_list")}>Portfolio</button>
		</section>
	);
}

export default Page;